.dark-border {
  border: 1px solid black;
  border-collapse: collapse;
  border-radius: 3px;
}
.login-register-button {
  background-color: #3f2d88;
}

.alert-box {
  /* background-color: #88cc88; */
  border-radius: 3px;
}
.align-right-icon {
  float: right;
  cursor: pointer;
}
.align-inline-end-icon {
  float: inline-end;
}
.bg-light-red {
  background-color: #f8d7da;
}
.bg-light-green {
  background-color: #b1ebbb;
}

.col-3 input {
  box-sizing: border-box;
  width: 100%;
}
.loading-container {
  margin: 45vh auto;
}
